import React from 'react';
import {FaFacebookSquare , FaInstagram , FaLinkedin , FaYoutube , FaMobileAlt  , FaEnvelope } from 'react-icons/fa';

import './SocialCard.css';

const SocialCard = () => {
    return(
        <div class="middle">
            <a class="btn" href="#">
              <FaFacebookSquare/>
            </a>
            <a class="btn" href="#">
            <FaInstagram/>
            </a>
            <a class="btn" href="#">
             <FaLinkedin/>
            </a>
            <a class="btn" href="#">
            <FaYoutube/>
            </a>
            <a class="btn" href="#">
            <i class="fab fa-youtube"></i>
            </a>
      </div>
    )
}

export default SocialCard;